.header__link {
  background-color: $logo-color;
  padding: 10px 12px 10px 10px;
  border-radius: 22px;
  display: flex;
  transition: $transition-fast;

  &:hover {
    background-color: $logo-color-hover;
    transition: $transition-fast;
  }

  &:active {
    background-color: $logo-color-active;
    transition: $transition-fast;
  }
}

.header__link-img {
}

.header__link-text {
  color: $white;
  margin: 0 0 0 10px;
  border-bottom: 1px solid transparent;
  
  &:hover {
    border-bottom: 1px solid $white;
  }
}

.header__social-networks {
  list-style-type: none;
  padding: 0;
  display: flex;
  margin: 0;
}
.header__social-networks-vk {
  width: 30px;
  height: 30px;
  margin: 0 10px 0 0;
  
  g circle {
    fill: transparent;
  }
  
  g path {
    fill: #fff;
  }
}
